.vidList {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	align-content: flex-start;
	article {
		width: 32%;
		height: 450px;
		border-top: 1px solid rgb(110, 109, 109);
		padding-top: 2vh;
		margin-bottom: 40px;
		.pic {
			display: block;
			width: 100%;
			height: 60%;
			padding: 10px;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				opacity: 0.8;
				transform: translateY(10%);
				transition: 0.5s;
				&:hover {
					opacity: 1;
					transform: translateY(0%);
				}
			}
		}
		.con {
			width: 100%;
			height: 40%;
			padding: 20px;
			h2 {
				font: bold 20px/1.2 'arial';
				color: rgb(203, 119, 10, 0.8);
				margin-bottom: 15px;
			}
			p {
				font: 14px/1.4 'arial';
				color: #a9a9a9;
				margin-bottom: 20px;
			}
			span {
				font: 16px/1 'arial';
				color: rgb(110, 109, 109);
			}
		}
	}
}

