// middle - 935px--------------------

@media screen and (max-width: $middle) {
	.vidList {
		article {
			width: 49%;
			margin-bottom: 40px;
		}
	}
}

// mobile - 539px -------------------
@media screen and (max-width: $mobile) {
	.vidList {
		article {
			width: 100%;
			margin-bottom: 25px;
		}
	}
}
