.modal {
	width: 100%;
	height: 100vh;
	background: rgba(0, 0, 0, 0.9);
	padding: 7vh 5vw;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 5;

	.box {
		width: 100%;
		height: 100%;

		img,
		video,
		iframe {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border: none;
		}
	}
	.close {
		position: absolute;
		top: 1vh;
		right: 5vw;
		font-size: 50px;
		font-weight: bold;
		color: rgb(203, 119, 10);
		cursor: pointer;
		transform: rotate(45deg);
		transition: scale 0.5s;
		&:hover {
			text-shadow: 1px 1px 5px rgba(255, 255, 255, 0.5);
		}
	}
}

// tablet - 1179px--------------------

@media screen and (max-width: $tablet) {
	.modal {
		padding: 8vw;
	}
}

// middle - 935px--------------------

@media screen and (max-width: $middle) {
	.modal {
		padding: 15vh 4vw;
	}
}

// mobile - 539px -------------------
@media screen and (max-width: $mobile) {
	.modal {
		padding: 25vh 4vw;
	}
}
